.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s linear;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10;
  display: flex;
}